import { NgOptimizedImage } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { User } from '@biz-nest/auth-client';
import {
  DeliveryNote,
  EcommerceOrder,
  Estimate,
  Expense, Invoice, JobCard,
  OperationalLocation,
  Receipt,
  Staff
} from '@biz-nest/commerce-client';
import { FrontendLogger, Iuserperm } from '@biz-nest/core-client';
import {
  AppNameService, AuthService, DataService, EventbusService,
  MainBarTitleService, WindowService, scrollToTop
} from '@front-shared-lib';
import { DeliveryManager, VendorManager, navByBus } from '@main-shared-lib';

@Component({
  selector: 'app-profile',
  standalone: true,
  imports: [
    NgOptimizedImage
  ],
  templateUrl: './profile.component.html',
  styleUrl: './profile.component.scss'
})
export class ProfileComponent
implements OnInit, OnDestroy {
  changePass = false;
  personalDetails = true;
  percentageDone = 100;
  styleWidth?: string;
  user?: Staff | VendorManager | DeliveryManager;
  permissions?: Iuserperm;
  id: string;
  updatingPerms = false;
  cleanup?: () => void;
  last5Estmates: Estimate[] = [];
  last5Invoices: Invoice[] = [];
  last5DeliveryNotes: DeliveryNote[] = [];
  last5JobCards: JobCard[] = [];
  last5OperationalLocations: OperationalLocation[] = [];
  last5Receipts: Receipt[] = [];
  last5Expenses: Expense[] = [];
  countInvoices = 0;
  countEstimates = 0;
  countExpenses = 0;
  countUsers = 0;
  countOrders = 0;
  countJobCards = 0;
  countOperationalLocations = 0;
  countReceipts = 0;
  orders: EcommerceOrder[] = [];
  invoices: Invoice[] = [];
  estimates: Estimate[] = [];
  expenses: Expense[] = [];
  jobCards: JobCard[] = [];
  operationalLocations: OperationalLocation[] = [];
  receipts: Receipt[] = [];
  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  constructor(
    public dataService: DataService,
    private windowService: WindowService,
    private router: Router,
    private route: ActivatedRoute,
    private eventbus: EventbusService,
    private auth: AuthService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) {
    this.id = this.route.snapshot?.params['urId'];
  }

  ngOnInit(): void {
    this.mainBarTitle.title = 'Profile';
    this.setTitle();
    // this.relegateProfileActivity();
    switch (this.dataService.profileType) {
      case 'staff':
        this.user = this.dataService.staffProfile;
        break;
      case 'vendorManager':
        this.user = this.dataService.vendorManagerProfile;
        break;
      case 'deliveryManager':
        this.user = this.dataService.deliveryManagerProfile;
        break;
    }
    scrollToTop(this.windowService);
    this.relegateProfileActivity();
  }

  setTitle() {
    this.mainBarTitle.title = 'Profile For' + this.userName;
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Easily view and manage your profile settings for personalized account management.'
    });
  }

  get userName() {
    if (this.user instanceof Staff) {
      return this.user?.commerceUser?.firstName + ' ' + this.user?.commerceUser?.lastName;
    }

    return this.user?.manager?.firstName + ' ' + this.user?.manager?.lastName;
  }

  relegateProfileActivity() {
    this.getInvoices();
    this.getEstimates();
    this.getExpenses();
    this.getJobCards();
    this.getOperationalLocations();
    this.getReceipts();
    this.getUsers();
    this.getOrders();
  }

  async getUsers() {
    try {
      const { count, dataArr } = await User.getAll();

      this.countUsers = count;
      this.dataService.users = dataArr;
    } catch (error) {
      FrontendLogger.error('getUsers err ', error);
    }
  }

  async getOrders() {
    try {
      const { count, dataArr } = await EcommerceOrder.getAll();

      this.countOrders = count;
      this.orders = dataArr;
    } catch (error) {
      FrontendLogger.error('getOrders err ', error);
    }
  }

  async getInvoices() {
    const { count, dataArr } = await Invoice
      .getAll();

    this.countInvoices = count;
    this.invoices = dataArr;
    this.last5Invoices = this.getLast5Things(this.invoices) as Invoice[];
  }

  async getEstimates() {
    const { count, dataArr } = await Estimate
      .getAll();

    this.countEstimates = count;
    this.estimates = dataArr;
    this.last5Estmates = this.getLast5Things(this.estimates) as Estimate[];
  }

  async getExpenses() {
    const { count, dataArr } = await Expense
      .getAll();

    this.countExpenses = count;
    this.expenses = dataArr;
    this.last5Expenses = this.getLast5Things(this.expenses) as Expense[];
  }

  async getJobCards() {
    const { count, dataArr } = await JobCard
      .getAll();

    this.countJobCards = count;
    this.jobCards = dataArr;
    this.last5JobCards = this.getLast5Things(this.jobCards) as JobCard[];
  }

  async getOperationalLocations() {
    const { count, dataArr } = await OperationalLocation
      .getAll();

    this.countOperationalLocations = count;
    this.operationalLocations = dataArr;
    this.last5OperationalLocations = this.getLast5Things(this.operationalLocations) as OperationalLocation[];
  }

  async getReceipts() {
    const { count, dataArr } = await Receipt
      .getAll();

    this.countReceipts = count;
    this.receipts = dataArr;
    this.last5Receipts = this.getLast5Things(this.receipts) as Receipt[];
  }

  about() {
    this.changePass = false;
    this.personalDetails = true;
    // document.getElementById('about').classList.add('active');
    // document.getElementById('pass').classList.remove('active');
  }

  pass() {
    this.changePass = true;
    this.personalDetails = false;
    // document.getElementById('about').classList.remove('active');
    // document.getElementById('pass').classList.add('active');
  }

  getLast5Things(data: Estimate[] |
    Invoice[] |
    DeliveryNote[] |
    JobCard[] |
    OperationalLocation[] |
    Receipt[] |
    Expense[]) {
    let limit = 0;

    if (data.length > 5) {
      limit = data.length - 5;
    }

    return data.filter((val, index) => index >= limit) as unknown;
  }

  navigate(url: string) {
    this.router.navigate([url]);
  }

  logOut() {
    this.auth.isLoggedIn = true;
    this.auth.logOut();
  }

  ngOnDestroy() {
    this.cleanup?.();
  }
}

